import BeneficiaryTypeService from '@/services/beneficiary/BeneficiaryTypeService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let BeneficiaryTypeTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultBeneficiaryTypeTableFilter: {
      type: String,
      default: null
    },
    defaultBeneficiaryTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      beneficiaryTypeTableData: [],
      beneficiaryTypeTableFilterTimer: null,
      beneficiaryTypeTableFilter: this.defaultBeneficiaryTypeTableFilter,
      beneficiaryTypeTableOrderBy: this.defaultBeneficiaryTypeTableOrderBy,
      beneficiaryTypeTableContainer: 'loading-container',
      beneficiaryTypeTableBusy: false,
      beneficiaryTypeTableTotalRows: 0,
      beneficiaryTypeTableCurrentPage: 1,
      beneficiaryTypeTablePerPage: 10
    }
  },
  computed: {
    beneficiaryTypeTableNoDataText: function () {
      return this.beneficiaryTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    beneficiaryTypeTableTotalPages: function () {
      return Math.ceil(this.beneficiaryTypeTableTotalRows / this.beneficiaryTypeTablePerPage)
    },
    beneficiaryTypeTablePagingInfo: function () {
      return this.pagingInfo(this.beneficiaryTypeTableCurrentPage, this.beneficiaryTypeTablePerPage, this.beneficiaryTypeTableTotalRows)
    }
  },
  methods: {
    loadBeneficiaryTypeTableData (loadingContainerId) {
      let self = this
      this.beneficiaryTypeTableBusy = true
      this.beneficiaryTypeTableContainer = loadingContainerId || this.beneficiaryTypeTableContainer
      this.startLoading(this.beneficiaryTypeTableContainer)
      BeneficiaryTypeService.beneficiaryTypes({
        pageIndex: this.beneficiaryTypeTableCurrentPage - 1,
        pageSize: this.beneficiaryTypeTablePerPage,
        filter: this.beneficiaryTypeTableFilter,
        orderByField: this.beneficiaryTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.beneficiaryTypeTableContainer)
        self.beneficiaryTypeTableBusy = false
        self.beneficiaryTypeTableTotalRows = response.data.totalRows
        self.beneficiaryTypeTableData = response.data.beneficiaryTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.beneficiaryTypeTableContainer)
        self.beneficiaryTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshBeneficiaryTypeTable () {
      this.loadBeneficiaryTypeTableData()
    },
    filterBeneficiaryTypeTable (filter) {
      let self = this
      if (this.beneficiaryTypeTableFilter !== filter) {
        clearTimeout(this.beneficiaryTypeTableFilterTimer)
        this.beneficiaryTypeTableFilterTimer = setTimeout(() => {
          self.beneficiaryTypeTableFilter = filter
          self.loadBeneficiaryTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.beneficiaryTypeTableFilterTimer)
      }
    },
    orderBeneficiaryTypeTable (orderBy, active) {
      this.beneficiaryTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadBeneficiaryTypeTableData()
    }
  },
  watch: {
    beneficiaryTypeTableCurrentPage: function () {
      this.refreshBeneficiaryTypeTable()
    },
    beneficiaryTypeTablePerPage: function () {
      this.refreshBeneficiaryTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshBeneficiaryTypeTable()
    }
  }
}

export default BeneficiaryTypeTableMixin
